:local .root {
  cursor: pointer;
  display: flex;
  overflow: hidden;
  align-items: center;
  box-sizing: border-box;
  font-weight: bold;
  text-transform: uppercase;
  user-select: none;
  padding: 0 var(--padding-small);
  border-width: 3px;
  border-color: var(--menu-item--color);
  color: var(--menu-item--color);
  background: var(--menu-item--background);
  text-decoration: none;
}

:local .root.left {
  border-left-style: solid;
  justify-content: flex-start;
}

:local .root.top {
  border-top-style: solid;
}

:local .root.right {
  border-right-style: solid;
  justify-content: flex-end;
}

:local .root.bottom {
  border-bottom-style: solid;
}

:local .root.left,
:local .root.right {
  margin-bottom: 2px;
  width: 100%;
  min-height: 24px;
}

:local .root.top,
:local .root.bottom {
  height: 100%;
  min-width: 100px;
  margin-right: 2px;
  justify-content: center;
}

:local .root.selected {
  color: var(--menu-item--selected-color);
  border-color: var(--menu-item--selected-color);
  background: var(--menu-item--selected-background);
}

:local .root:hover {
  color: var(--menu-item--hover-color);
  border-color: var(--menu-item--hover-color);
}

:local .root:last-child {
  margin: 0;
}




