:local .content {
  padding: var(--padding-big);
  box-sizing: border-box;
}

:local .navigtaion {
  position: fixed;
  width: var(--article--navigation);
  max-height: calc(100vh - var(--toolbar--height) - var(--padding-big) * 3);
  overflow-x: hidden;
  overflow-y: auto;
  background: var(--color--accent);
  box-shadow: var(--shadow--medium);
}

:local .navigtaion::-webkit-scrollbar {
  width: 5px;
}

:local .navigtaion::-webkit-scrollbar-track {
  background: transparent;
}

:local .navigtaion::-webkit-scrollbar-thumb {
  background: var(--app--font-color);
}




