:local .root {
  --svg-icon--color: var(--color--text-blur);
  --svg-icon--hover-color: var(--color--highlight);
  display: flex;
  overflow: hidden;
  height: var(--toolbar--height);
  background: var(--toolbar--background);
  position: fixed;
  width: 100%;
  z-index: 99999;
  box-shadow: var(--shadow--medium);
}

:local .layer {
  display: flex;
  flex: 1;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 0 var(--padding-big);
  box-sizing: border-box;
}

:local .layer nav {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}




