:local {
    .root {
        display: flex;
        flex: 1;
        position: relative;
    }
    .container {
        display: flex;
        height: 100%;
    }
    .resizer {
        height: 100%;
        width: 10px;
        cursor: col-resize;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}


