:local .root {
  display: flex;
  flex-direction: column;
  height: calc(100vh - var(--toolbar--height) - var(--padding-big) * 3);
  box-sizing: border-box;
  box-shadow: var(--shadow--medium);
  background: var(--code-view--background);
}

:local .controls {
  display: flex;
  height: 40px;
  box-sizing: border-box;
  flex-shrink: 0;
}

:local .container {
  box-sizing: border-box;
  background: var(--code-view--container-background);
  height: 100%;
  width: 100%;
  overflow: hidden;
  padding: var(--padding-small);
}

:local .file {
  display: inline-block;
}

:local .tab {
  box-sizing: border-box;
  margin: 0;
  padding: var(--padding-small) var(--padding-medium);
  outline: none;
  background: transparent;
  border: 0;
  color: var(--app--font-color);
  cursor: pointer;
  user-select: none;
  background: var(--code-view--tab-background);
}

:local .tab:hover {
  background: var(--code-view--tab-selected-background);
}

:local .tab.tabSelected {
  background: var(--code-view--tab-selected-background);
}




